* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: gray;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.country-list {
  width: 1000px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

img {
  width: 150px;
  height: 100px;
}

input {
  font: inherit;
  max-width: 320px;
  margin-bottom: 16px;
  padding: 10px;
}

.inputBox {
  width: 250px;
  position: relative;
}

.inputBox #search-box {
  width: 100%;
  background: gray;
  border: 1px solid #2b57b8;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  font-size: 1em;
}

.inputBox span {
  pointer-events: none;
  text-transform: uppercase;
  padding: 10px;
  font-size: 1em;
  transition: all .5s;
  position: absolute;
  left: 10px;
}

.inputBox input:valid ~ span, .inputBox input:valid ~ span {
  color: #2b57b8;
  transform: translateX(10px), translateY(-10px);
  background: gray;
  padding: 0 10px;
  font-size: .65em;
  top: -10px;
}

